button {
  margin: 10px 10px 0 0;
}
.btnItem {
  margin: 10px 20px;
}
.editGoodImg {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
}
.editGoodImg > img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
